@use "_var.scss";

.icon {
  width: 2.5rem;
  height: 2.5rem;
  background-color: var(--color-white);
  border-radius: 50px;
  border: 2px solid var(--color-dark-gray);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s cubic-bezier(0.64, 0, 0.78, 0);
  cursor: pointer;
  position: absolute; 

  &:hover,
  &:focus {
    background-color: white;
    transition: 0.3s cubic-bezier(0.22, 1, 0.36, 1);
  }

  &:active {
    transform: scale(0.95);    
  }

  &--sound {
    left: 1rem;
    bottom: 2rem;
    z-index: 30;
  }
  &--next {
    right: 1rem;
    bottom: 2rem;
    z-index: 20;

    &:hover .arrow--next {
      transform: translateX(1px);
    }
  }
  &--prev {
    right: 4.5rem;
    bottom: 2rem;
    z-index: 10;

    &:hover .arrow--prev {
      transform: translateX(-1px);
    }
  }

  &--close{
    background-color: var(--color-black);
    border: 2px solid var(--color-white);
    top: -1.25rem;
    right: -1.25rem;

    &:hover,
    &:focus {
      background-color: var(--color-dark-gray);
    }
  }

  &--audio,
  &--photo{
    border: none;
    width: 3rem;
    height: 3rem;
    transform: scaleX(0);
  }  

  &__svg{
    height: 1rem;
    width: auto;
    position: absolute;
  }

  &__border{
    width: 100%;
    height: 100%;
    border: 2px dotted var(--color-black);
    border-radius: 50%;
    box-sizing: border-box;
    position: relative;
  }

  &--audio1{
    top: 40%;
    left: 70%;    
  }
  &--audio2{
    top: 30%;
    left: 20%;    
  }
  &--photo1{
    top: 60%;
    left: 20%;
  }
  &--audio3{
    top: 40%;
    left: 80%;    
  }
  &--audio4{
    top: 20%;
    left: 30%;    
  }
  &--audio5{
    top: 60%;
    left: 70%;    
  }
  &--audio6{
    top: 40%;
    left: 50%;    
  }
  &--photo2{
    top: 60%;
    left: 20%;
  }
  &--photo3{
    top: 50%;
    left: 30%;
  }
  &--photo4{
    top: 30%;
    left: 30%;
  }
}

.sound--none{
  display: none;
}
.sound,
.arrow,
.sound--none,
.close {
  width: 1rem;
  transition: 0.3s cubic-bezier(0.22, 1, 0.36, 1);
}
.circle {
  height: 3rem;
  width: 3rem;  
  border-radius: 50%;
  border: 2px solid var(--color-white);
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.22, 1, 0.36, 1);
  position: relative;
  opacity: 0;

  &:hover,
  &:focus {
    background-color: var(--color-white);
  }

  &--text {
    &::before{
      content: "";
      position: absolute;
      top: -2rem;
      left: 50%;
      transform: translateX(-50%);
      color: var(--color-white);
      font-size: 1rem;
      letter-spacing: 1px;
    }
  }
  &--avant {
    &::before{
      content: "AVANT";
    }
  }
  &--arriere {
    &::before{
      content: "ARRIÈRE";
    }
  }
  &--oui {
    &::before{
      content: "OUI";
    }
  }
  &--non {
    &::before{
      content: "NON";
    }
  }
  &--continuer {
    &::before{
      content: "CONTINUER";
    }
  }
  &--retourner {
    &::before{
      content: "RETOURNER";
    }
  }
}
.line{
  border-bottom: 2px dotted var(--color-white);
  width: 12rem;
}
.btn {
  text-decoration: none;
  color: var(--color-white);
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 3rem;
  background-color: var(--color-white);
  padding: 2px;

  :hover,
  :focus{
    border-radius: 0;    
  }
  :active {
    background-color: black;
  }
  &__cadre{
    background-color: var(--color-black);
    padding: 1rem 2rem;
    border-radius: 0 0 25px 25px;
    transition:  0.3s cubic-bezier(0.22, 1, 0.36, 1);;
  }
}
@media (min-width: 768px){
  .icon {
    width: 3rem;
    height: 3rem;

    &--sound {
      left: 2rem;
      bottom: 2.5rem;
    }
    &--next {
      right: 2rem;
      bottom: 2.5rem;
    }
    &--prev {
      right: 6rem;
      bottom: 2.5rem;
    }
    &--close{
      top: -1.5rem;
      right: -1.5rem;
    }
    &--audio,
    &--photo{
      width: 3.5rem;
      height: 3.5rem;
    }  

  }
  .line{
    width: 16rem;
  }
  .btn {
    font-size: 1.25rem;
    letter-spacing: 2px;
    padding: 4px;
    
      &__cadre{
        padding: 1rem 2rem;
        border-radius: 0 0 25px 25px;
      }
  }

}

@media (min-width: 1440px){
  .icon {  
    &--sound {
      left: 3rem;
      bottom: 2.5rem;
    }
    &--next {
      right: 3rem;
      bottom: 2.5rem;
    }
    &--prev {
      right: 7rem;
      bottom: 2.5rem;
    }
  }
}